import { zhiCardFactory as cardFactory } from '@ali/4ever-factory';
import locale from '../../../locales/zh_CN';
import createRenderNode from './createRenderNode';
import { BlockLinkCard, LinkPluginConfig } from '../types';
import { LinkCardPlugin } from '@ali/4ever-bi';
import { LinkPlugin } from '@ali/4ever-bamboo';

const { copyLink } = LinkCardPlugin.utils;
const { LINK_CARD_COPY_LINK } = LinkCardPlugin.actions;


const createZhiCalendarCardPlugin = cardFactory((config: { link?: LinkPluginConfig, clipboard?: { serializer?: any } }) => {
  const linkConfig = {
    locale: { ...locale.link.locale },
    ...config.link
  };

  const serializer = config.clipboard?.serializer;

  return {
    cardType: 'link',
    renderNode: createRenderNode(linkConfig, false),
    queries: {
      getMinTableColWidth: LinkPlugin.getMinTableColWidthForBlockLinkCard
    },
    onAction: (action, controller, next) => {
      const { type, payload } = action;

      if (type === LINK_CARD_COPY_LINK) {
        const { node } = payload as { node: BlockLinkCard };
        return copyLink(controller, node, serializer || controller.serializer);
      }

      return next();
    }
  };
});

export default createZhiCalendarCardPlugin;
